document.addEventListener('DOMContentLoaded', function(){
    initAPP()
})

function initAPP(){
    fixNavegation();
    CreateGallery();
    scrollNav();
}

// scroll navigation

function fixNavegation(){
    const bar = document.querySelector('.header');
    const festivalInfo = document.querySelector('.festival-info')
    const body = document.querySelector('body');

    window.addEventListener('scroll', function() {
        if( festivalInfo.getBoundingClientRect().bottom < 0  ) {
            bar.classList.add('fix');
            body.classList.add('body-scroll');
        } else {
            bar.classList.remove('fix');
            body.classList.remove('body-scroll');
        }
    });
}

function scrollNav(){
    const links = document.querySelectorAll('.navbar a')

    links.forEach(link => {
        link.addEventListener('click', function(e){
            e.preventDefault();
            const scroll = e.target.attributes.href.value;
            const section = document.querySelector(scroll);
            section.scrollIntoView({behavior: 'smooth'});
        })
        
    });
}


// Show images and, max img
function CreateGallery(){
    const gallery = document.querySelector('.gallery-img')
    for (let i = 1; i <= 12; i++) {
        const img = document.createElement('picture')
        img.innerHTML =  `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="./build/img/thumb/${i}.jpg" alt="gallery">
        `;

        img.onclick = function(){
            ShowImage(i)
        }
        gallery.appendChild(img)
             
    }
}

function ShowImage(image){
    const gallery = document.querySelector('.gallery-img')
    const img = document.createElement('picture')
    img.innerHTML =  `
        <source srcset="build/img/big/${image}.avif" type="image/avif">
        <source srcset="build/img/big/${image}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="./build/img/big/${image}.jpg" alt="gallery">
        `
    /* overlay with image */
    const overlay = document.createElement('div')
    overlay.appendChild(img)
    overlay.classList.add('overlay')
    overlay.onclick = function(){
        const body = document.querySelector('body')
        body.classList.remove('fix-body')
        overlay.remove();
    }

    /* button delete */
    const closeImg = document.createElement('p')
    closeImg.textContent = 'X'
    closeImg.classList.add('btn-close')
    closeImg.onclick = function(){
        const body = document.querySelector('body')
        body.classList.remove('fix-body')
        overlay.remove();
    }
    overlay.appendChild(closeImg)

    /*  image to  html*/
    const body = document.querySelector('body')
    body.appendChild(overlay)
    body.classList.add('fix-body')


}

